@import url("https://fonts.googleapis.com/css2?family=Mulish:wght@400;700&display=swap");

body {
    background: #303236;
    font-family: Mulish;
}

.component--search {
    background: #3d3f43;
    border-radius: 15px;
    padding: 40px 45px;
    box-shadow: 0px 15px 25px -20px #202125;
    form {
        position: relative;
        margin-bottom: 30px;
        .search-icon {
            position: absolute;
            top: 0;
            left: 0;
            width: 53px;
            height: 50px;
            display: flex;
            align-items: center;
            justify-content: center;
        }
        input {
            padding-left: 53px;
            height: 50px;
            background: #303236;
            border: none;
            border-radius: 5px;
            font-size: 16px;
            color: #fff;
            &:focus {
                outline: 0;
            }

            &::placeholder {
                color: rgba(#fff, 0.5);
            }
        }
    }

    h6 {
        font-size: 12px;
        font-weight: bold;
        color: #fff;
        text-transform: uppercase;
    }

    ul {
        margin-top: 13px;
        list-style: none;
        li {
            a {
                display: block;
                text-decoration: none;
                font-size: 16px;
                color: #fff;
                padding: 7px 0;

                &:hover {
                    color: #2ccdea;
                }
            }
            &.active a {
                color: #2ccdea;
            }
        }
    }
}

.component--filter-search {
    background: #3d3f43;
    border-radius: 5px;
    form {
        height: 68px;
        padding: 14px;
        display: flex;
        .input {
            display: flex;
            align-items: center;
            flex: 1;

            .search-icon {
                width: 50px;
                text-align: center;
            }
            input {
                width: 100%;
                background-color: transparent;
                border: none;
                height: 40px;
                font-size: 16px;
                color: #fff;
                padding-left: 53px;
                &:focus {
                    outline: 0;
                }

                &::placeholder {
                    color: rgba(#fff, 0.5);
                }
            }
        }
        ul {
            display: flex;
            margin-left: 15px;
            li {
                list-style-type: none;
                label {
                    .text {
                        display: blok;
                        height: 40px;
                        line-height: 40px;
                        font-size: 16px;
                        color: #fff;
                        padding: 10px 25px;
                        cursor: pointer;
                        border-radius: 40px;
                        transition: 400ms background-color, 400ms color;
                    }
                    input {
                        display: none;
                        &:checked + .text {
                            background-color: #303236;
                            color: #2ccdea;
                        }
                    }
                }
            }
        }
    }
}

.component--filter {
    background: #3d3f43;
    border-radius: 15px;
    padding: 40px 45px;
    margin: 25px;
    box-shadow: 0px 15px 25px -20px #202125;
    form {
        ul {
            li {
                display: flex;
                margin-bottom: 20px;
                align-items: center;
                font-size: 16px;
                font-weight: bold;
                color: #fff;

                &:last-child {
                    margin-bottom: 0;
                }

                .key {
                    width: 100px;
                }
                .value {
                    flex: 1;
                    input,
                    select {
                        // padding: 0 0 0 20px;
                        height: 50px;
                        width: 100%;
                        background: #303236;
                        border: none;
                        border-radius: 5px;
                        font-size: 16px;
                        color: #fff;
                        &:focus {
                            outline: 0;
                        }

                        &::placeholder {
                            color: rgba(#fff, 0.5);
                        }
                    }
                    select {
                        appearance: none;
                        background-image: #303236 url(../../img/arrow-down.svg);
                        background-repeat: no-repeat;
                        background-position: calc(100% -20px) center;
                    }
                }

                &.filter-button {
                    margin-top: 40px;
                    border-top: 1px solid #303236;
                    padding-top: 25px;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;
                    a{
                        color: rgba(#fff, .5);
                        text-decoration: none;
                        font-size: 16px;
                        &:hover{
                            color: #fff;
                        }
                    }

                    button {
                        height: 50px;
                        padding: 0 30px;
                        display: flex;
                        align-items: center;
                        background: linear-gradient(to right, #31A5d9, #1e96a0);
                        font-size: 16px;
                        color: #fff;
                        border: none;
                        box-shadow: 0px 7px 10px -5px rgba(43, 45, 48, 0.604513);
                        border-radius: 25px;
                        cursor: pointer;
                    }
                }
            }
        }
    }
}
